import React from 'react';
import { connect } from 'react-redux';

import { queryUserStatuses } from 'store/actions/dictionary';
import SimpleAppBar from 'components/SimpleAppBar';
import useEffectOnce from 'hooks/useEffectOnce';
import StatusList from './StatusList';
import useStyles from './useStyles';

const mapStateToProps = ({ dictionary }) => {
  return {
    statuses: dictionary.userStatuses,
  };
};

const UserStatus = ({ statuses, dispatch }) => {
  // Styles hook
  const classes = useStyles();

  // Query data effect hook
  useEffectOnce(() => {
    dispatch(queryUserStatuses());
  });

  const handleSelectStatus = (item) => {
    console.log(item);
  };

  return (
    <div className={classes.root}>
      <SimpleAppBar caption="Статусы" />

      <StatusList items={statuses} onClick={handleSelectStatus} />
    </div>
  );
};

export default connect(mapStateToProps, null)(UserStatus);
