import React from 'react';
import { withRouter } from 'react-router-dom';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import IconDriveEta from '@material-ui/icons/DriveEta';
import IconInbox from '@material-ui/icons/Inbox';
import IconContacts from '@material-ui/icons/Contacts';
import Fab from '@material-ui/core/Fab';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';

import ImgMascot from 'img/mascot-login-page.png';
import { clearToken } from 'system/auth';
import ROUTES from 'system/routes';
import {
  AppDispatchContext,
  openDrawer as openDrawerAction,
  closeDrawer as closeDrawerAction,
} from '../state';
import useStyles from './useStyles';

const Drawer = ({ open, history }) => {
  const classes = useStyles();

  const dispatch = React.useContext(AppDispatchContext);

  const openDrawer = () => dispatch(openDrawerAction());
  const closeDrawer = () => dispatch(closeDrawerAction());

  const navigateTo = (route) => {
    closeDrawer();
    history.replace(route);
  };

  const logout = () => {
    closeDrawer();
    clearToken();
    history.replace(ROUTES.LOGIN);
  };

  return (
    <SwipeableDrawer open={open} onClose={closeDrawer} onOpen={openDrawer}>
      <div className={classes.content}>
        <div className={classes.userContainer}>
          <Avatar
            classes={{ root: classes.avatar, img: classes.avatarImg }}
            alt="User"
            src={ImgMascot}
          />
          <Typography variant="h6">Вася Пупкин</Typography>
          <Typography variant="subtitle1">курьер</Typography>
        </div>

        <Divider />

        <List className={classes.list}>
          <ListItem
            className={classes.textListItem}
            button
            onClick={() => navigateTo(ROUTES.RESTAURANTS)}
          >
            <ListItemText>
              <Typography variant="overline">Точка</Typography>
              <Typography variant="body1">Васильки 1</Typography>
            </ListItemText>
          </ListItem>

          <ListItem
            className={classes.textListItem}
            button
            onClick={() => navigateTo(ROUTES.USER_STATUS)}
          >
            <ListItemText>
              <Typography variant="overline">Статус</Typography>
              <Typography variant="body1">Перерыв с 10.00 до 10.30</Typography>
            </ListItemText>
          </ListItem>
        </List>

        <Divider />

        <List className={classes.list}>
          <ListItem
            className={classes.iconListItem}
            button
            onClick={() => navigateTo(ROUTES.ORDERS)}
          >
            <ListItemIcon>
              <IconDriveEta color="primary" />
            </ListItemIcon>
            <ListItemText primary="Доставки" />
          </ListItem>

          <ListItem
            className={classes.iconListItem}
            button
            onClick={() => navigateTo(ROUTES.MESSAGES)}
          >
            <ListItemIcon>
              <IconInbox color="primary" />
            </ListItemIcon>
            <ListItemText primary="Сообщения" />
          </ListItem>

          <ListItem
            className={classes.iconListItem}
            button
            onClick={() => navigateTo(ROUTES.CONTACTS)}
          >
            <ListItemIcon>
              <IconContacts color="primary" />
            </ListItemIcon>
            <ListItemText primary="Контакты" />
          </ListItem>
        </List>

        <div className={classes.bottomContainer}>
          <Fab
            className={classes.action}
            variant="extended"
            size="medium"
            color="secondary"
            aria-label="Add"
          >
            Закрыть смену
          </Fab>

          <Fab
            className={classes.action}
            variant="extended"
            size="medium"
            color="secondary"
            aria-label="Add"
            onClick={logout}
          >
            Выйти
          </Fab>
        </div>
      </div>
    </SwipeableDrawer>
  );
};

export default withRouter(Drawer);
