import { QUERY_RESTAURANTS } from '../actions/restaurant';
import { createReducer, processAsyncAction } from '../utils';

const STATE = {
  loading: false,
  restaurants: [],
};

const reducerObject = {
  [QUERY_RESTAURANTS]: (state, action) =>
    processAsyncAction(state, action, {
      loading: 'loading',
      data: 'restaurants',
    }),
};

export default createReducer(reducerObject, STATE);
