import React from 'react';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';

const ORANGE = '#FF936B';
const BLUE_LIGHT = '#DAEDFD';
const BLUE = '#2196F3';
const GREEN = '#00DA77';
const WHITE = '#FFF';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: BLUE,
    },
    secondary: {
      main: ORANGE,
    },
  },
  typography: {
    overline: {
      color: 'rgba(0, 0, 0, 0.54)',
    },
  },
  overrides: {
    MuiFab: {
      root: {
        color: WHITE,
        backgroundColor: GREEN,
      },
    },
    MuiTooltip: {
      tooltip: {
        fontSize: '0.75rem',
      },
    },
  },
  app: {
    palette: {
      orderStatus: {
        pending: ORANGE,
        assigned: BLUE_LIGHT,
        delivery: BLUE,
      },
    },
  },
});

const RestfrontMuiTheme = ({ children }) => (
  <MuiThemeProvider theme={theme}>{children}</MuiThemeProvider>
);

export default RestfrontMuiTheme;
