import * as apiOrder from 'api/order';
import { queryApi } from '../utils';

const PREFIX = 'order';

export const QUERY_ORDERS = `${PREFIX}-QUERY_ORDERS`;
export const queryOrders = queryApi(QUERY_ORDERS, apiOrder.fetchOrderList);

export const FETCH_ORDER = `${PREFIX}-FETCH_ORDER`;
export const fetchOrder = queryApi(FETCH_ORDER, apiOrder.fetchOrder);
