import React from 'react';
import PropTypes from 'prop-types';

import {
  formatClient,
  formatAddress,
  calculateTotalSum,
  getDeliveryStatus,
  getDeliveryStatusName,
} from 'data/order';
import { formatMoney } from 'system/format';
import OSMMap from 'components/OSMMap';
import InfoItem from './InfoItem';
import useStyles from './useStyles';

const propTypes = {
  order: PropTypes.shape({}).isRequired,
};

const OrderInfo = ({ order, ...restProps }) => {
  // Styles hook
  const classes = useStyles({ classes: restProps.classes });

  // Order delivery status
  const statusName = React.useMemo(() => {
    const deliveryStatus = getDeliveryStatus(order.status);
    return getDeliveryStatusName(deliveryStatus);
  }, [order.status]);

  return (
    <>
      <InfoItem classes={{ root: classes.statusBar }} label="Статус">
        {statusName}
      </InfoItem>

      <InfoItem label="ФИО">{formatClient(order)}</InfoItem>
      <InfoItem label="Адрес">{formatAddress(order)}</InfoItem>

      <div className={classes.map}>
        <OSMMap />
      </div>

      <InfoItem classes={{ value: classes.orderLines }} label="Заказ">
        {order.lines.map((line) => (
          <div key={line.id} className={classes.orderLine}>
            <div className={classes.lineName}>{line.good.name}</div>
            <div className={classes.lineQuantity}>{line.quantity}</div>
          </div>
        ))}
      </InfoItem>

      {order.comment && <InfoItem label="Прим.">{order.comment}</InfoItem>}

      <InfoItem label="Сумма">{formatMoney(calculateTotalSum(order))}</InfoItem>
    </>
  );
};

OrderInfo.propTypes = propTypes;

export default OrderInfo;
