import React from 'react';
import { connect } from 'react-redux';

import { queryRestaurants } from 'store/actions/restaurant';
import SimpleAppBar from 'components/SimpleAppBar';
import useEffectOnce from 'hooks/useEffectOnce';
import RestaurantList from './RestaurantList';
import useStyles from './useStyles';

const mapStateToProps = ({ restaurant }) => {
  return {
    restaurants: restaurant.restaurants,
  };
};

const Restaurants = ({ restaurants, dispatch }) => {
  // Styles hook
  const classes = useStyles();

  // Query data effect hook
  useEffectOnce(() => {
    dispatch(queryRestaurants());
  });

  const handleSelectRestaurant = (restaurant) => {
    console.log(restaurant);
  };

  return (
    <div className={classes.root}>
      <SimpleAppBar caption="Точки доставки" />

      <RestaurantList
        restaurants={restaurants}
        onClick={handleSelectRestaurant}
      />
    </div>
  );
};

export default connect(mapStateToProps, null)(Restaurants);
