import React from 'react';
import { connect } from 'react-redux';

import { queryOrders } from 'store/actions/order';
import ROUTES from 'system/routes';
import MenuAppBar from 'components/MenuAppBar';
import useEffectOnce from 'hooks/useEffectOnce';
import OrderList from './OrderList';
import EmptyScreen from './EmptyScreen';
import useStyles from './useStyles';

const mapStateToProps = ({ order }) => {
  return {
    orders: order.orders,
  };
};

const Orders = ({ orders, history, dispatch }) => {
  // Styles hook
  const classes = useStyles();

  // Query data effect hook
  useEffectOnce(() => {
    dispatch(queryOrders());
  });

  const navigateToOrder = (order) => {
    history.push(`${ROUTES.ORDER}/${order.id}`);
  };

  return (
    <div className={classes.root}>
      <MenuAppBar caption="Доставки" />

      {orders.length > 0 ? (
        <OrderList orders={orders} onOrderClick={navigateToOrder} />
      ) : (
        <EmptyScreen />
      )}
    </div>
  );
};

export default connect(mapStateToProps, null)(Orders);
