import React from 'react';
import { withRouter } from 'react-router-dom';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

import { login } from 'api/auth';
import { writeToken } from 'system/auth';
import ROUTES from 'system/routes';
import ImgMascot from 'img/mascot-login-page.png';
import ImgLogo from 'img/logo.svg';
import { addNotification, AppDispatchContext } from '../Root/state';
import useStyles from './useStyles';

const Login = ({ history }) => {
  const classes = useStyles();

  // Local state hook
  const dispatch = React.useContext(AppDispatchContext);

  // Form state
  const [username, setUsername] = React.useState('');
  const [password, setPassword] = React.useState('');

  const handleSubmit = (e) => {
    e.preventDefault();

    if (!username || !password) {
      return;
    }

    login(username, password)
      .then((response) => {
        writeToken(response.token);
        history.replace(ROUTES.HOME);
      })
      .catch((e) => {
        dispatch(addNotification('warning', 'Ошибка входа'));
        console.log(e);
      });
  };

  const checkAuthentication = () => null;

  // Check authentication on load, and navigate to HOME if already logged in
  React.useEffect(() => {
    checkAuthentication();
  }, []);

  return (
    <div className={classes.root}>
      <img className={classes.logo} src={ImgLogo} alt="Logo" />
      <img className={classes.mascot} src={ImgMascot} alt="Mascot" />

      <form className={classes.form} onSubmit={handleSubmit}>
        <TextField
          className={classes.textField}
          label="Логин"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
          autoComplete="username"
          autoFocus
          margin="normal"
          variant="outlined"
          required
          fullWidth
        />

        <TextField
          className={classes.textField}
          label="Пароль"
          type="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          autoComplete="current-password"
          margin="normal"
          variant="outlined"
          required
          fullWidth
        />

        <Button
          type="submit"
          variant="contained"
          color="primary"
          className={classes.submit}
        >
          Войти
        </Button>
      </form>
    </div>
  );
};

export default withRouter(Login);
