import { QUERY_USER_STATUSES } from '../actions/dictionary';
import { createReducer, processAsyncAction } from '../utils';

const STATE = {
  userStatusesLoading: false,
  userStatuses: [],
};

const reducerObject = {
  [QUERY_USER_STATUSES]: (state, action) =>
    processAsyncAction(state, action, {
      loading: 'userStatusesLoading',
      data: 'userStatuses',
    }),
};

export default createReducer(reducerObject, STATE);
