import { DateTime } from 'luxon';

import { Http, unwrapJsonResponse } from './utils';

export const fetchOrderList = () => {
  const url = '/gedemin/order';
  const query = {
    dateBegin: DateTime.local().toISODate(),
    dateEnd: DateTime.local().toISODate(),
  };
  return Http.get(url, { query }).then(unwrapJsonResponse);
};

export const fetchOrder = (id) => {
  const url = `/gedemin/order/${id}`;
  return Http.get(url).then(unwrapJsonResponse);
};
