import React from 'react';
import { connect } from 'react-redux';

import { fetchOrder } from 'store/actions/order';
import SimpleAppBar from 'components/SimpleAppBar';
import OrderInfo from './OrderInfo';
import useStyles from './useStyles';

const mapStateToProps = ({ order }, { match }) => {
  const orderId = Number(match.params.id);
  return {
    order: order.orders.find((order) => order.id === orderId),
  };
};

const Order = ({ order, match, dispatch }) => {
  // Styles hook
  const classes = useStyles();

  // Query data effect hook
  React.useEffect(() => {
    const orderId = Number(match.params.id);
    dispatch(fetchOrder(orderId));
  }, [dispatch, match.params.id]);

  const orderNumber = order ? order.number : '';
  const appCaption = `Доставка${orderNumber ? ' № ' + orderNumber : ''}`;

  return (
    <div className={classes.root}>
      <SimpleAppBar caption={appCaption} />

      {order ? <OrderInfo order={order} /> : <span>Empty order</span>}

      <div className={classes.actions} />
    </div>
  );
};

export default connect(mapStateToProps, null)(Order);
