import React from 'react';
import MuiSnackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';
import IconClose from '@material-ui/icons/Close';

import useFreshRef from 'hooks/useFreshRef';
import { removeNotification, AppDispatchContext } from '../state';
import useStyles from './useStyles';

const TIMEOUT = 3000;

const Snackbar = ({ notifications, ...restProps }) => {
  // Style hook
  const classes = useStyles({ classes: restProps.classes });

  // Open/closed state
  const [open, setOpen] = React.useState(false);
  const [current, setCurrent] = React.useState(null);
  const currentRef = useFreshRef(current);

  // Local state hook
  const dispatch = React.useContext(AppDispatchContext);

  // Effect to change current notification
  React.useEffect(() => {
    const last =
      notifications.length > 0 ? notifications[notifications.length - 1] : null;
    if (currentRef.current) {
      dispatch(removeNotification(currentRef.current.id));
      setOpen(false);
    }
    setCurrent(last);

    console.log(notifications);
  }, [dispatch, currentRef, notifications]);

  // Effect to hide current notification
  React.useEffect(() => {
    if (!current) {
      setOpen(false);
      return;
    }

    setOpen(true);
    const timeout = setTimeout(
      () => dispatch(removeNotification(current.id)),
      TIMEOUT,
    );
    return () => clearTimeout(timeout);
  }, [dispatch, current]);

  const handleClose = (event, reason) => {
    if (!current || reason === 'clickaway') {
      return;
    }

    dispatch(removeNotification(current.id));
  };

  return (
    <MuiSnackbar
      anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
      open={open}
      onClose={handleClose}
      ContentProps={{
        'aria-describedby': 'message-id',
      }}
      message={current ? <span id="message-id">{current.message}</span> : null}
      action={[
        <IconButton
          className={classes.buttonClose}
          key="close"
          aria-label="Close"
          color="inherit"
          onClick={handleClose}
        >
          <IconClose />
        </IconButton>,
      ]}
    />
  );
};

export default Snackbar;
