export const STATUS = {
  pending: 'pending',
  assigned: 'assigned',
  delivery: 'delivery',
};
const STATUS_NAMES = {
  [STATUS.pending]: 'Новая',
  [STATUS.assigned]: 'Принята',
  [STATUS.delivery]: 'В пути',
};

const SERVER_STATUS = {
  DEFERRED: 0, // отложенная
  NEW: 1, // новый
  RECEIVED: 2, // получен из внешней системы
  IN_PROCESS: 3, // в обработке
  CANCELED: 4, // отменен менеджером доставки
  SENDED_TO_RESTAURANT: 5, // отправлен в ресторан
  COOKING: 6, // готовится в ресторане
  READY: 7, // готов в ресторане
  CANCELED_BY_RESTAURANT: 8, // отменен рестораном
  TAKEN_BY_CLIENT: 9, // забран клиентом

  SENDED_TO_COURIER: 10, // отправлен курьеру
  TAKEN_BY_COURIER: 11, // забран с ресторана
  DELIVERED: 12, // доставлен
  CANCELED_BY_COURIER: 13, // отменен курьером
  CANCELED_BY_CLIENT: 14, // отменен клиентом
};

export const getDeliveryStatus = (order) => {
  if (order.status === SERVER_STATUS.TAKEN_BY_COURIER) {
    return STATUS.delivery;
  }
  if (order.status === SERVER_STATUS.SENDED_TO_COURIER) {
    return STATUS.assigned;
  }
  return STATUS.pending;
};

export const getDeliveryStatusName = (status) => STATUS_NAMES[status];

const append = (str, part, divider = ', ') =>
  (str ? `${str}${divider}` : '') + part;

export const formatAddress = (order) => {
  if (!order) {
    return '';
  }
  let result = '';
  if (order.city) {
    result = append(result, `г. ${order.city}`);
  }
  if (order.street) {
    result = append(result, `ул. ${order.street}`);
  }
  if (order.house) {
    result = append(result, `д. ${order.house}`);
  }
  if (order.housing) {
    result = append(result, `к. ${order.housing}`);
  }
  if (order.entrance) {
    result = append(result, `под. ${order.entrance}`);
  }
  if (order.floor) {
    result = append(result, `эт. ${order.floor}`);
  }
  if (order.apartment) {
    result = append(result, `кв. ${order.apartment}`);
  }
  return result;
};

export const formatClient = (order) => {
  if (!order || !order.client) {
    return '';
  }
  const client = order.client;
  let result = '';
  if (client.surName) {
    result = append(result, client.surName, ' ');
  }
  if (client.firstName) {
    result = append(result, client.firstName, ' ');
  }
  if (client.middleName) {
    result = append(result, client.middleName, ' ');
  }
  return result;
};

export const calculateTotalSum = (order) =>
  order.lines.reduce((acc, line) => acc + line.sumWithDiscountNcu, 0);
