import has from 'lodash/has';

export const ASYNC_STATUS = {
  loading: 'loading',
  success: 'success',
  error: 'error',
};

export const queryApi = (actionType, promiseFunc) => (...args) => async (
  dispatch,
) => {
  dispatch({
    type: actionType,
    status: ASYNC_STATUS.loading,
  });

  try {
    const data = await promiseFunc(...args);
    dispatch({
      type: actionType,
      status: ASYNC_STATUS.success,
      payload: { data },
    });
  } catch (error) {
    dispatch({
      type: actionType,
      status: ASYNC_STATUS.error,
      payload: { error },
    });
  }
};

export const processAsyncAction = (state, action, fields) => {
  const { status, payload } = action;

  switch (status) {
    case ASYNC_STATUS.success:
      return {
        ...state,
        [fields.loading]: false,
        [fields.data]: payload.data || [],
      };

    case ASYNC_STATUS.error:
      return {
        ...state,
        [fields.loading]: false,
      };

    default:
      return {
        ...state,
        [fields.loading]: true,
      };
  }
};

export const createReducer = (reducerObject, initialState = {}) => (
  state = initialState,
  action,
) =>
  has(reducerObject, action.type)
    ? reducerObject[action.type](state, action)
    : state;
