const appendAddress = (address, part) => (address ? `${address}, ` : '') + part;

export const formatAddress = (restaurant) => {
  if (!restaurant || !restaurant.restData) {
    return '';
  }
  const data = restaurant.restData;
  let address = '';
  if (data.city) {
    address = appendAddress(address, `г. ${data.city}`);
  }
  if (data.street) {
    address = appendAddress(address, `ул. ${data.street}`);
  }
  if (data.house) {
    address = appendAddress(address, `д. ${data.house}`);
  }
  if (data.housing) {
    address = appendAddress(address, `к. ${data.housing}`);
  }
  return address;
};
