import numeral from 'numeral';

export const formatMoney = (value) => {
  //
  const prepared = Math.abs(value || 0);
  const rounded = Math.round(prepared * 100) / 100;

  const intPart = Math.trunc(rounded);
  const fractPart = Math.round((rounded - intPart) * 100);

  const intStr = numeral(intPart).format('0,0');
  const fractStr = numeral(fractPart).format('00');

  return `${value < 0 ? '- ' : ''}${intStr} руб. ${fractStr} коп.`;
};
