import React from 'react';

import { createReducer } from 'store/utils';
import { genID } from 'system/utils';

const OPEN_DRAWER = 'open-drawer';
export const openDrawer = () => ({
  type: OPEN_DRAWER,
});

const CLOSE_DRAWER = 'close-drawer';
export const closeDrawer = () => ({
  type: CLOSE_DRAWER,
});

const NOTIFICATION_ADD = 'notification-add';
export const addNotification = (variant, message) => ({
  type: NOTIFICATION_ADD,
  notification: {
    id: genID(),
    variant,
    message,
  },
});

const NOTIFICATION_REMOVE = 'notification-remove';
export const removeNotification = (id) => ({
  type: NOTIFICATION_REMOVE,
  id,
});

const INITIAL_STATE = {
  drawerOpened: false,
  notifications: [],
};

const reducerObject = {
  [OPEN_DRAWER]: (state, action) => {
    return {
      ...state,
      drawerOpened: true,
    };
  },

  [CLOSE_DRAWER]: (state, action) => {
    return {
      ...state,
      drawerOpened: false,
    };
  },

  [NOTIFICATION_ADD]: (state, action) => {
    const { notification } = action;
    return {
      ...state,
      notifications: [...state.notifications, notification],
    };
  },

  [NOTIFICATION_REMOVE]: (state, action) => {
    const { id } = action;
    return {
      ...state,
      notifications: state.notifications.filter((n) => n.id !== id),
    };
  },
};
const reducer = createReducer(reducerObject, INITIAL_STATE);

export const useLocalState = () => React.useReducer(reducer, INITIAL_STATE);

export const AppDispatchContext = React.createContext(null);
