import { QUERY_ORDERS, FETCH_ORDER } from '../actions/order';
import { createReducer, processAsyncAction, ASYNC_STATUS } from '../utils';

const STATE = {
  loading: false,
  orders: [],
};

const arrayIncludes = (array, findObj) =>
  array.some((obj) => obj.id === findObj.id);

const arrayAdd = (array, newObj) => [...array, newObj];

const arrayReplace = (array, newObj) =>
  array.map((obj) => (obj.id === newObj.id ? newObj : obj));

const arrayAddOrReplace = (array, obj) =>
  arrayIncludes(array, obj) ? arrayReplace(array, obj) : arrayAdd(array, obj);

const reducerObject = {
  [QUERY_ORDERS]: (state, action) =>
    processAsyncAction(state, action, {
      loading: 'loading',
      data: 'orders',
    }),

  [FETCH_ORDER]: (state, action) => {
    const { status, payload } = action;
    if (status === ASYNC_STATUS.success) {
      return {
        ...state,
        orders: arrayAddOrReplace(state.orders, payload.data || null),
      };
    }
    return state;
  },
};

export default createReducer(reducerObject, STATE);
