import React from 'react';
import { Provider } from 'react-redux';
import {
  BrowserRouter as Router,
  Route,
  Redirect,
  Switch,
} from 'react-router-dom';

import store from 'store';
import RestfrontMuiTheme from 'components/RestfrontMuiTheme';
import PrivateRoute from 'components/PrivateRoute';
import { isAuthenticated } from 'system/auth';
import ROUTES from 'system/routes';
import PageLogin from '../Login';
import PageOrders from '../Orders';
import PageOrder from '../Order';
import PageMessages from '../Messages';
import PageContacts from '../Contacts';
import PageRestaurants from '../Restaurants';
import PageUserStatus from '../UserStatus';
import { useLocalState, AppDispatchContext } from './state';
import Drawer from './Drawer';
import Snackbar from './Snackbar';
import useStyles from './useStyles';

const Root = () => {
  // Styles hook
  const classes = useStyles();

  // Local state hook
  const [state, appDispatch] = useLocalState();
  const { drawerOpened, notifications } = state;

  return (
    <Provider store={store}>
      <RestfrontMuiTheme>
        <Router>
          <AppDispatchContext.Provider value={appDispatch}>
            <div className={classes.root}>
              <Snackbar notifications={notifications} />

              {isAuthenticated() && (
                <>
                  <header className={classes.header} />
                  <Drawer open={drawerOpened} />
                </>
              )}

              <Switch>
                <Route path={ROUTES.LOGIN}>
                  <PageLogin />
                </Route>

                <PrivateRoute
                  exact
                  path={ROUTES.ORDERS}
                  component={PageOrders}
                />
                <PrivateRoute
                  path={`${ROUTES.ORDER}/:id`}
                  component={PageOrder}
                />
                <PrivateRoute
                  exact
                  path={ROUTES.MESSAGES}
                  component={PageMessages}
                />
                <PrivateRoute
                  exact
                  path={ROUTES.CONTACTS}
                  component={PageContacts}
                />

                <PrivateRoute
                  exact
                  path={ROUTES.RESTAURANTS}
                  component={PageRestaurants}
                />
                <PrivateRoute
                  exact
                  path={ROUTES.USER_STATUS}
                  component={PageUserStatus}
                />

                <Redirect to={ROUTES.ORDERS} />
              </Switch>
            </div>
          </AppDispatchContext.Provider>
        </Router>
      </RestfrontMuiTheme>
    </Provider>
  );
};

export default Root;
